<template>
  <div class="container segment">
    <img v-for="(card, index) in cards" :key="index" :src="`../static/cards/${card}`">
  </div>
</template>

<script>
import cards from '../assets/cards.json'
export default {
  name: 'Cards',
  data () {
    return {
      cards
    }
  }
}
</script>

<style scoped>
img {
  padding: 10px;
  border-radius: 4px;
  width: 20%
}
img:hover {
  background-color: #f1f1f1;
}
div {
  text-align: center;
}
</style>



