<template>
  <div class="container segment">
    <img v-for="(equip, index) in equips" :key="index" :src="`../static/equip/${equip}`">
  </div>
</template>

<script>
import equips from '../assets/equip.json'
export default {
  name: 'Cards',
  data () {
    return {
      equips
    }
  }
}
</script>

<style scoped>
img {
  padding: 10px;
  border-radius: 4px;
  width: 10%
}
img:hover {
  background-color: #f1f1f1;
}
div {
  text-align: center;
}
</style>



