<template>
  <div class="container segment">
    <img v-for="(frame, index) in frames" :key="index" :src="`../static/frames/${frame}`">
  </div>
</template>

<script>
import frames from '../assets/frames.json'
export default {
  name: 'Frames',
  data () {
    return {
      frames
    }
  }
}
</script>

<style scoped>
img {
  padding: 10px;
  border-radius: 4px;
  width: 10%
}
img:hover {
  background-color: #f1f1f1;
}
div {
  text-align: center;
}
</style>



