<template>
  <div class="container segment">
    <img v-for="(sticker, index) in stickers" :key="index" :src="`../static/stickers/Mood${sticker}.png`">
  </div>
</template>

<script>
export default {
  name: 'Stickers',
  data () {
    return {
      stickers: []
    }
  },
  methods: {
    generate () {
      let arr = [0, 0, 0]
      for (let i = 1; i <= 49; i++) {
        if (arr[2] === 9) {
          arr[1]++
          arr[2] = 0
        } else if (arr[1] === 9) {
          arr[0]++
          arr[2] = 0
        } else {
          arr[2]++
        }
        this.stickers.push(arr.join(''))
      }
    }
  },
  mounted: function () {
    this.generate()
  }
}
</script>

<style scoped>
img {
  padding: 10px;
  border-radius: 4px;
}
img:hover {
  background-color: #f1f1f1;
}
div {
  text-align: center;
}
</style>



