<template>
  <div class="container segment">
    <img v-for="(classs, index) in classes" :key="index" :src="`../static/classes/${classs}`">
  </div>
</template>

<script>
import classes from '../assets/classes.json'
export default {
  name: 'Cards',
  data () {
    return {
      classes
    }
  }
}
</script>

<style scoped>
img {
  padding: 10px;
  border-radius: 4px;
  width: 10%
}
img:hover {
  background-color: #f1f1f1;
}
div {
  text-align: center;
}
</style>



